.movieCard {
    list-style: none;
    font-size: 1.5rem;
    text-align: center;
    transition: 0.5s;
}

.movieCard:hover {
    opacity: 0.4;
    transform: translateY(-30px);
    transition: 0.5s;
}

.movieImage {
    border-radius: 10px;
}

.title {
    color: white;
}

@media(max-width: 560px) {
    .movieImage {
        width: 90%;
        height: auto;
    }
}